<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
  >
    <transition name="translate" mode="out-in">
      <div :key="text.leaseTerm.title">
        <wra-card-text v-if="showValidation" class="pa-4">
          <wra-validation-tooltip
            type="wra-error"
            role="status"
            aria-live="polite"
          >
            {{ validationMessage }}
          </wra-validation-tooltip>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.leaseTerm.title"></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0">
            <p v-html="text.leaseTerm.description"></p>
          </wra-card-text>
        </wra-card-text>

        <wra-card-text>
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.leaseTerm.startDate.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.leaseTerm.startDate.description + ' ' + today"></p>
          </wra-card-text>
          <wra-text-field
            v-model="leaseStartDateString"
            v-mask="'##/##/####'"
            :placeholder="text.leaseTerm.placeholder"
            class="ltt-date-field"
            id="leaseStartId"
            inputmode="numeric"
          ></wra-text-field>
          <div v-if="showStartDateValidation">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ text.leaseTerm.validation.startDate }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <wra-card-text>
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.leaseTerm.endDate.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p
              v-html="text.leaseTerm.endDate.description + ' ' + tenYearsLater"
            ></p>
          </wra-card-text>
          <wra-text-field
            v-model="leaseEndDateString"
            v-mask="'##/##/####'"
            :placeholder="text.leaseTerm.placeholder"
            class="ltt-date-field"
            id="leaseEndId"
            inputmode="numeric"
          ></wra-text-field>
          <div v-if="showEndDateValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ text.leaseTerm.validation.endDate }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <wra-card-actions class="py-2 px-4">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="nextStage()"
                id="nextButton"
              >
                {{ text.next }}
                <wra-icon alt="" right large>navigate_next</wra-icon>
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButon"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </div>
    </transition>
  </wra-card>
</template>

<script>
export default {
  props: ["text", "inputs"],

  data: () => ({
    isStartDateValid: true,
    isEndDateValid: true,
    isPageValid: true,
    showStartDateValidation: false,
    showEndDateValidation: false,
    showValidation: false,
    validationMessage: null,
    localInputs: {},
    leaseStartDateString: null,
    leaseEndDateString: null,
    today: null,
    tenYearsLater: null
  }),
  watch: {
    text: {
      deep: true,
      handler() {
        this.validatePage();
      }
    },
    leaseStartDateString: {
      handler() {
        this.showStartDateValidation = false;
      }
    },
    leaseEndDateString: {
      handler() {
        this.showEndDateValidation = false;
      }
    }
  },
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event(
          "LTT Calculator",
          "What are the lease start and end dates?",
          "Back"
        );
      }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      this.validatePage();
      var isValid = true;
      if (this.isPageValid == false) {
        isValid = false;
        this.showValidation = true;
      } else {
        this.showValidation = false;
      }
      if (this.isStartDateValid == false) {
        isValid = false;
        this.showStartDateValidation = true;
      } else {
        this.showStartDateValidation = false;
      }
      if (this.isEndDateValid == false) {
        isValid = false;
        this.showEndDateValidation = true;
      } else {
        this.showEndDateValidation = false;
      }

      if (isValid == false) {
        this.$emit("scrollUp");
      } else {
        if (this.$root.$data.cookieAccept.websiteUse == "true") {
          this.$ga.event(
            "LTT Calculator",
            "What is the lease start date?",
            this.localInputs.leaseStartDate
          );

          this.$ga.event(
            "LTT Calculator",
            "What is the lease end date?",
            this.localInputs.leaseEndDate
          );
        }
        this.$emit("next", this.localInputs);
      }
    },
    validatePage: function() {
      if (this.leaseStartDateString == null) {
        this.isStartDateValid = false;
      } else {
        // We need to do this bit because JS thinks dates are written month/day/year, so we switch the first two around.
        var leaseStartDate = new Date(
          this.leaseStartDateString.split("/")[1] +
            "/" +
            this.leaseStartDateString.split("/")[0] +
            "/" +
            this.leaseStartDateString.split("/")[2]
        );

        var enteredStartMonth =
          parseInt(this.leaseStartDateString.split("/")[1]) - 1;

        if (leaseStartDate == "Invalid Date") {
          this.isStartDateValid = false;
        } else if (enteredStartMonth != leaseStartDate.getMonth()) {
          this.isStartDateValid = false;
        } else {
          this.isStartDateValid = true;
          this.localInputs.leaseStartDate = new Intl.DateTimeFormat(
            "en-GB"
          ).format(leaseStartDate);
        }
      }

      if (this.leaseEndDateString == null) {
        this.isEndDateValid = false;
      } else {
        // We need to do this bit because JS thinks dates are written month/day/year, so we switch the first two around.
        var leaseEndDate = new Date(
          this.leaseEndDateString.split("/")[1] +
            "/" +
            this.leaseEndDateString.split("/")[0] +
            "/" +
            this.leaseEndDateString.split("/")[2]
        );

        var enteredEndMonth =
          parseInt(this.leaseEndDateString.split("/")[1]) - 1;

        if (leaseEndDate == "Invalid Date") {
          this.isEndDateValid = false;
        } else if (enteredEndMonth != leaseEndDate.getMonth()) {
          this.isEndDateValid = false;
        } else {
          this.isEndDateValid = true;
          this.localInputs.leaseEndDate = new Intl.DateTimeFormat(
            "en-GB"
          ).format(leaseEndDate);
        }
      }
      /* Only check if user has set calculationDate to 'EffectiveDate' */
      if (this.localInputs.calculationDate == "EffectiveDate") {
        var transactionDate = new Date(
          this.localInputs.transactionDate.split("/")[1] +
            "/" +
            this.localInputs.transactionDate.split("/")[0] +
            "/" +
            this.localInputs.transactionDate.split("/")[2]
        );

        if (leaseEndDate < transactionDate) {
          this.validationMessage = this.text.leaseTerm.validation.effectiveDate;
          this.isPageValid = false;
          return;
        } else {
          this.isPageValid = true;
        }
      }

      /* This must always be checked */
      if (leaseEndDate < leaseStartDate) {
        this.validationMessage = this.text.leaseTerm.validation.leaseStartDate;
        this.isPageValid = false;
        this.$emit("scrollUp");
      } else {
        this.isPageValid = true;
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));

    this.today = new Intl.DateTimeFormat("en-GB").format(new Date());

    var tenYearsLaterDate = new Date();

    tenYearsLaterDate.setFullYear(tenYearsLaterDate.getFullYear() + 10);

    this.tenYearsLater = new Intl.DateTimeFormat("en-GB").format(
      tenYearsLaterDate
    );

    // Need code here to create a formatted string of the date for the text field, to add to 'leaseStartDateString' and 'leaseEndDateString'
    if (this.localInputs.leaseStartDate) {
      this.leaseStartDateString = this.localInputs.leaseStartDate;
    }
    if (this.localInputs.leaseEndDate) {
      this.leaseEndDateString = this.localInputs.leaseEndDate;
    }
  },
  mounted() {
    this.$emit("scrollUp");
  }
};
</script>

<style scoped>
.v-input {
  font-size: 18px;
}
</style>
