<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
  >
    <transition name="translate" mode="out-in">
      <div :key="text.rentInformation.title">
        <wra-card-text
          class="pa-4"
          v-if="
            showYearOneValidation ||
              showYearTwoValidation ||
              showYearThreeValidation ||
              showYearFourValidation ||
              showYearFiveValidation ||
              showRelevantRentValidation ||
              showHighestRentValidation
          "
        >
          <wra-validation-tooltip
            type="wra-error"
            role="status"
            aria-live="polite"
          >
            {{ text.rentInformation.validation.errorOnPage }}
          </wra-validation-tooltip>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.rentInformation.title"></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0">
            <p v-html="text.rentInformation.description"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- This should always be visible -->
        <wra-card-text>
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.yearOne.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.yearOne.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.rentYearOne"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="rentYearOne"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showYearOneValidation">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageOne }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <!-- Show if leaseDuration > 1 year -->
        <wra-card-text v-if="leaseDuration.yearsSpanned > 1">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.yearTwo.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.yearTwo.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.rentYearTwo"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="rentYearTwo"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showYearTwoValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageTwo }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <!-- Show if leaseDuration > 2 years -->
        <wra-card-text v-if="leaseDuration.yearsSpanned > 2">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.yearThree.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.yearThree.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.rentYearThree"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="rentYearThree"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showYearThreeValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageThree }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <!-- Show if leaseDuration > 3 years -->
        <wra-card-text v-if="leaseDuration.yearsSpanned > 3">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.yearFour.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.yearFour.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.rentYearFour"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="rentYearFour"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showYearFourValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageFour }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <!-- Show if leaseDuration > 4 years -->
        <wra-card-text v-if="leaseDuration.yearsSpanned > 4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.yearFive.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.yearFive.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.rentYearFive"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="rentYearFive"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showYearFiveValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageFive }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <!-- This should always be visible -->
        <wra-card-text>
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.relevantRent.title"></h3>
          </wra-card-title>
          <wra-card-text class="px-0 pt-0 pb-4">
            <p>
              {{ text.rentInformation.relevantRent.guidance.textP1 }}
              <a
                :href="text.rentInformation.relevantRent.guidance.link"
                target="_blank"
                rel="noreferrer"
                :aria-label="
                  text.rentInformation.relevantRent.guidance.linkText +
                    ' (' +
                    text.rentInformation.relevantRent.guidance.newTab +
                    ')'
                "
                >{{ text.rentInformation.relevantRent.guidance.linkText }}</a
              >
              {{ text.rentInformation.relevantRent.guidance.textP2 }}
            </p>
          </wra-card-text>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.relevantRent.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.relevantRent"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="relevantRent"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showRelevantRentValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageRelevantRent }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <!-- Show if leaseDuration > 5 years -->
        <wra-card-text v-if="leaseDuration.yearsSpanned > 5">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h3 v-html="text.rentInformation.highestRent.title"></h3>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.rentInformation.highestRent.description"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.highestRent"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            id="highestRent"
            inputmode="decimal"
          ></wra-text-field>
          <div v-if="showHighestRentValidation" class="pb-5">
            <wra-validation-tooltip
              type="wra-error"
              role="status"
              aria-live="polite"
            >
              {{ validationMessageHighestRent }}
            </wra-validation-tooltip>
          </div>
        </wra-card-text>

        <wra-card-actions class="py-2 px-4">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="nextStage()"
                id="nextButton"
              >
                {{ text.next }}
                <wra-icon alt="" right large>navigate_next</wra-icon>
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButton"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </div>
    </transition>
  </wra-card>
</template>

<script>
import createNumberMask from "text-mask-addons/dist/createNumberMask";
const currencyMask = createNumberMask({
  prefix: "",
  allowDecimal: true,
  includeThousandsSeparator: true,
  allowNegative: false
});
export default {
  props: ["text", "inputs", "leaseDuration"],

  data: () => ({
    isYearOneValid: true,
    isYearTwoValid: true,
    isYearThreeValid: true,
    isYearFourValid: true,
    isYearFiveValid: true,
    isRelevantRentValid: true,
    isHighestRentValid: true,
    showYearOneValidation: false,
    showYearTwoValidation: false,
    showYearThreeValidation: false,
    showYearFourValidation: false,
    showYearFiveValidation: false,
    showRelevantRentValidation: false,
    showHighestRentValidation: false,
    localInputs: {},
    mask: currencyMask,
    validationMessageOne: null,
    validationMessageTwo: null,
    validationMessageThree: null,
    validationMessageFour: null,
    validationMessageFive: null,
    validationMessageRelevantRent: null,
    validationMessageHighestRent: null
  }),
  watch: {
    text: {
      deep: true,
      handler() {
        this.validatePage();
      }
    }
  },
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "What are the rent amounts?", "Back");
      }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      this.validatePage();
      var isValid = true;

      if (this.isYearOneValid == false) {
        isValid = false;
        this.showYearOneValidation = true;
      } else {
        this.showYearOneValidation = false;
      }
      if (this.isYearTwoValid == false) {
        isValid = false;
        this.showYearTwoValidation = true;
      } else {
        this.showYearTwoValidation = false;
      }
      if (this.isYearThreeValid == false) {
        isValid = false;
        this.showYearThreeValidation = true;
      } else {
        this.showYearThreeValidation = false;
      }
      if (this.isYearFourValid == false) {
        isValid = false;
        this.showYearFourValidation = true;
      } else {
        this.showYearFourValidation = false;
      }
      if (this.isYearFiveValid == false) {
        isValid = false;
        this.showYearFiveValidation = true;
      } else {
        this.showYearFiveValidation = false;
      }
      if (this.isRelevantRentValid == false) {
        isValid = false;
        this.showRelevantRentValidation = true;
      } else {
        this.showRelevantRentValidation = false;
      }
      if (this.isHighestRentValid == false) {
        isValid = false;
        this.showHighestRentValidation = true;
      } else {
        this.showHighestRentValidation = false;
      }

      if (isValid == false) {
        this.$emit("scrollUp");
      } else {
        if (this.$root.$data.cookieAccept.websiteUse == "true") {
          this.$ga.event(
            "LTT Calculator",
            "Rent year 1",
            this.localInputs.rentYearOne
          );

          if (this.leaseDuration.yearsSpanned >= 2) {
            this.$ga.event(
              "LTT Calculator",
              "Rent year 2",
              this.localInputs.rentYearTwo
            );
          }

          if (this.leaseDuration.yearsSpanned >= 3) {
            this.$ga.event(
              "LTT Calculator",
              "Rent year 3",
              this.localInputs.rentYearThree
            );
          }

          if (this.leaseDuration.yearsSpanned >= 4) {
            this.$ga.event(
              "LTT Calculator",
              "Rent year 4",
              this.localInputs.rentYearFour
            );
          }

          if (this.leaseDuration.yearsSpanned >= 5) {
            this.$ga.event(
              "LTT Calculator",
              "Rent year 5",
              this.localInputs.rentYearFive
            );
          }

          this.$ga.event(
            "LTT Calculator",
            "What is the relevant rent?",
            this.localInputs.relevantRent
          );

          if (this.leaseDuration.yearsSpanned > 5) {
            this.$ga.event(
              "LTT Calculator",
              "What is the highest rent payable in any consecutive 12 month period in the first 5 years?",
              this.localInputs.highestRent
            );
          }
        }
        this.$emit("next", this.localInputs);
      }
    },
    validatePage: function() {
      if (
        this.localInputs.rentYearOne == null ||
        this.localInputs.rentYearOne == ""
      ) {
        this.validationMessageOne = this.text.rentInformation.validation.yearOne;
        this.isYearOneValid = false;
      } else if (
        parseInt(this.localInputs.rentYearOne.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageOne = this.text.rentInformation.validation.aboveMax;
        this.isYearOneValid = false;
      } else {
        this.isYearOneValid = true;
      }

      if (this.leaseDuration.yearsSpanned < 2) {
        this.isYearTwoValid = true;
      } else if (
        this.localInputs.rentYearTwo == null ||
        this.localInputs.rentYearTwo == ""
      ) {
        this.validationMessageTwo = this.text.rentInformation.validation.yearTwo;
        this.isYearTwoValid = false;
      } else if (
        parseInt(this.localInputs.rentYearTwo.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageTwo = this.text.rentInformation.validation.aboveMax;
        this.isYearTwoValid = false;
      } else {
        this.isYearTwoValid = true;
      }

      if (this.leaseDuration.yearsSpanned < 3) {
        this.isYearThreeValid = true;
      } else if (
        this.localInputs.rentYearThree == null ||
        this.localInputs.rentYearThree == ""
      ) {
        this.validationMessageThree = this.text.rentInformation.validation.yearThree;
        this.isYearThreeValid = false;
      } else if (
        parseInt(this.localInputs.rentYearThree.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageThree = this.text.rentInformation.validation.aboveMax;
        this.isYearThreeValid = false;
      } else {
        this.isYearThreeValid = true;
      }

      if (this.leaseDuration.yearsSpanned < 4) {
        this.isYearFourValid = true;
      } else if (
        this.localInputs.rentYearFour == null ||
        this.localInputs.rentYearFour == ""
      ) {
        this.validationMessageFour = this.text.rentInformation.validation.yearFour;
        this.isYearFourValid = false;
      } else if (
        parseInt(this.localInputs.rentYearFour.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageFour = this.text.rentInformation.validation.aboveMax;
        this.isYearFourValid = false;
      } else {
        this.isYearFourValid = true;
      }

      if (this.leaseDuration.yearsSpanned < 5) {
        this.isYearFiveValid = true;
      } else if (
        this.localInputs.rentYearFive == null ||
        this.localInputs.rentYearFive == ""
      ) {
        this.validationMessageFive = this.text.rentInformation.validation.yearFive;
        this.isYearFiveValid = false;
      } else if (
        parseInt(this.localInputs.rentYearFive.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageFive = this.text.rentInformation.validation.aboveMax;
        this.isYearFiveValid = false;
      } else {
        this.isYearFiveValid = true;
      }

      if (
        this.localInputs.relevantRent == null ||
        this.localInputs.relevantRent == ""
      ) {
        this.validationMessageRelevantRent = this.text.rentInformation.validation.relevantRent;
        this.isRelevantRentValid = false;
      } else if (
        parseInt(this.localInputs.relevantRent.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageRelevantRent = this.text.rentInformation.validation.aboveMax;
        this.isRelevantRentValid = false;
      } else {
        this.isRelevantRentValid = true;
      }

      if (this.leaseDuration.yearsSpanned < 6) {
        this.isHighestRentValid = true;
      } else if (
        this.localInputs.highestRent == null ||
        this.localInputs.highestRent == ""
      ) {
        this.validationMessageHighestRent = this.text.rentInformation.validation.highestRent;
        this.isHighestRentValid = false;
      } else if (
        parseInt(this.localInputs.highestRent.replaceAll(",", "")) <
          parseInt(this.localInputs.rentYearOne.replaceAll(",", "")) ||
        parseInt(this.localInputs.highestRent.replaceAll(",", "")) <
          parseInt(this.localInputs.rentYearTwo.replaceAll(",", "")) ||
        parseInt(this.localInputs.highestRent.replaceAll(",", "")) <
          parseInt(this.localInputs.rentYearThree.replaceAll(",", "")) ||
        parseInt(this.localInputs.highestRent.replaceAll(",", "")) <
          parseInt(this.localInputs.rentYearFour.replaceAll(",", "")) ||
        parseInt(this.localInputs.highestRent.replaceAll(",", "")) <
          parseInt(this.localInputs.rentYearFive.replaceAll(",", ""))
      ) {
        this.validationMessageHighestRent = this.text.rentInformation.validation.belowRentValues;
        this.isHighestRentValid = false;
      } else if (
        parseInt(this.localInputs.highestRent.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessageHighestRent = this.text.rentInformation.validation.aboveMax;
        this.isHighestRentValid = false;
      } else {
        this.isHighestRentValid = true;
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.$emit("scrollUp");

    if (this.leaseDuration.yearsSpanned < 5) {
      this.localInputs.rentYearFive = null;
    }
    if (this.leaseDuration.yearsSpanned < 4) {
      this.localInputs.rentYearFour = null;
    }
    if (this.leaseDuration.yearsSpanned < 3) {
      this.localInputs.rentYearThree = null;
    }
    if (this.leaseDuration.yearsSpanned < 2) {
      this.localInputs.rentYearTwo = null;
    }
    if (this.leaseDuration.yearsSpanned < 6) {
      this.localInputs.highestRent = null;
    }
  }
};
</script>

<style scoped>
.v-input {
  font-size: 18px;
}
</style>
