<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
  >
    <transition name="translate" mode="out-in">
      <wra-card-text
        v-if="showValidation"
        class="pa-4"
        :key="text.calculationDate.title"
      >
        <wra-validation-tooltip
          type="wra-error"
          role="status"
          aria-live="polite"
        >
          {{ text.calculationDate.validation }}
        </wra-validation-tooltip>
      </wra-card-text>
    </transition>

    <transition name="translate" mode="out-in">
      <wra-card-text class="pa-4" :key="text.calculationDate.title">
        <wra-card-title class="pt-0 px-0 pb-4">
          <h2 v-html="text.calculationDate.title"></h2>
        </wra-card-title>
        <wra-card-text class="pt-0 px-0 pb-8">
          <p v-html="text.calculationDate.description"></p>
        </wra-card-text>

        <wra-radio-group v-model="localInputs.calculationDate">
          <template v-slot:label>
            {{ text.calculationDate.title }}
          </template>
          <wra-radio :value="text.calculationDate.answers[0].value">
            <template v-slot:label>
              <p v-html="text.calculationDate.answers[0].label"></p>
            </template>
          </wra-radio>
          <wra-radio :value="text.calculationDate.answers[1].value">
            <template v-slot:label>
              <p v-html="text.calculationDate.answers[1].label"></p>
            </template>
          </wra-radio>
        </wra-radio-group>
      </wra-card-text>
    </transition>

    <transition name="translate" mode="out-in">
      <wra-card-actions class="py-2 px-4" :key="text.calculationDate.title">
        <v-row>
          <v-col cols="12" md="2" order="first" order-md="last">
            <wra-button dark class="mb-2" @click="nextStage()" id="nextButton">
              {{ text.next }}
              <wra-icon alt="" right large>navigate_next</wra-icon>
            </wra-button>
          </v-col>
          <v-col cols="12" md="2" order="last" order-md="first">
            <wra-button
              dark
              class="mr-4 mb-2"
              @click="previousStage"
              id="backButton"
            >
              <wra-icon alt="" left large>navigate_before</wra-icon>
              <span v-html="text.previous"></span>
            </wra-button>
          </v-col>
        </v-row>
      </wra-card-actions>
    </transition>
  </wra-card>
</template>

<script>
export default {
  props: ["text", "inputs"],

  data: () => ({
    showValidation: false,
    localInputs: {}
  }),
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event(
          "LTT Calculator",
          "Which date should be used for the calculation?",
          "Back"
        );
      }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      if (!this.localInputs.calculationDate) {
        this.showValidation = true;
        this.$emit("scrollUp");
      } else {
        if (this.$root.$data.cookieAccept.websiteUse == "true") {
          this.$ga.event(
            "LTT Calculator",
            "Which date should be used for the calculation?",
            this.localInputs.calculationDate
          );
        }
        this.$emit("next", this.localInputs);
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.$emit("scrollUp");
  }
};
</script>
