<template>
  <v-container
    class="pt-0 pb-3"
    :class="{
      'px-0': $vuetify.breakpoint.smAndDown
    }"
  >
    <!--Heading-->
    <v-row
      no-gutters
      class="pb-6 hidden-print"
      :class="{
        'px-5': $vuetify.breakpoint.smAndDown
      }"
    >
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <v-divider
          role="presentation"
          style="height: 6px;
          max-height: 6px;
          background: #e5e5e5;
          border: none;"
        ></v-divider>

        <transition name="translate" mode="out-in">
          <wra-subheader class="mt-8" :key="text.header">
            {{ text.subheader }}
          </wra-subheader>
        </transition>
        <transition name="translate" mode="out-in">
          <h1 :key="text.header">
            {{ text.header }}
          </h1>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <!--Card-->
    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12" class="minimum-height">
        <transition name="fade" mode="out-in">
          <component
            id="questionCard"
            :is="componentName"
            v-if="loaded && textLoaded"
            :text="text"
            :inputs="inputs"
            :leaseDuration="leaseDuration"
            :leaseDisplay="leaseDisplay"
            v-on:previous="previousStage"
            v-on:next="nextStage"
            v-on:goToStage="goToStage"
            v-on:reset="reset"
            v-on:scrollUp="scrollUp"
          ></component>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <!--Help & Guidance-->
    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <transition name="translate" mode="out-in">
        <v-col
          lg="8"
          cols="12"
          class="pt-6 pb-3 hidden-print"
          :class="{
            'px-5': $vuetify.breakpoint.smAndDown
          }"
          :key="text.helpGuidance.helpLink"
        >
          <wra-button
            outlined
            class="my-8"
            :href="text.helpGuidance.helpLink"
            target="_blank"
            rel="noreferrer"
            :aria-label="
              text.helpGuidance.helpText + '(' + text.helpGuidance.newTab + ')'
            "
            id="helpLink"
          >
            {{ text.helpGuidance.helpText }}
            <wra-icon right size="24" class="mx-3">mdi-open-in-new</wra-icon>
          </wra-button>
        </v-col>
      </transition>
      <v-col lg="2" cols="0"></v-col>
    </v-row>
  </v-container>
</template>

<script>
import TransactionType from "../components/ltt-calculator/TransactionType.vue";
import TransactionRate from "../components/ltt-calculator/TransactionRate.vue";
import PropertyType from "../components/ltt-calculator/PropertyType.vue";
import TransactionDate from "../components/ltt-calculator/TransactionDate.vue";
import PurchasePrice from "../components/ltt-calculator/PurchasePrice.vue";
import TotalPremium from "../components/ltt-calculator/TotalPremium.vue";
import LeaseTerm from "../components/ltt-calculator/LeaseTerm.vue";
import CalculationDate from "../components/ltt-calculator/CalculationDate.vue";
import RentInformation from "../components/ltt-calculator/RentInformation.vue";
import TaxCalculation from "../components/ltt-calculator/TaxCalculation.vue";

const axios = require("axios");

export default {
  props: ["lang"],

  data: () => ({
    componentName: "",
    loaded: false,
    textLoaded: false,
    text: {
      transactionType: {
        answers: [{ label: {} }]
      },
      helpGuidance: {}
    },
    inputs: {
      transactionRate: null,
      propertyType: null,
      transactionDate: null,
      purchasePrice: null,
      /*totalPremium: null,*/
      leaseStartDate: null,
      leaseEndDate: null,
      calculationDate: null,
      rentYearOne: null,
      rentYearTwo: null,
      rentYearThree: null,
      rentYearFour: null,
      rentYearFive: null,
      relevantRent: null,
      highestRent: null
    }
  }),
  watch: {
    lang: {
      handler() {
        this.getText();
      }
    }
  },
  methods: {
    getText: function() {
      axios
        .get("./text/" + this.lang + "/lttcalculator.json")
        .then((response) => {
          this.text = response.data.text;
          this.textLoaded = true;
          document.title = this.text.documentTitleText;
        });
    },
    setStage: function() {
      this.componentName = "TransactionType";
      this.loaded = true;
    },
    previousStage: function(localInputs) {
      this.inputs = localInputs;
      // TransactionType - dealt with in child component ( this.$emit("reset") )

      //Residential Route
      if (this.inputs.transactionType == "Residential") {
        if (this.componentName == "TransactionRate") {
          this.componentName = "TransactionType";
        } else if (this.componentName == "TransactionDate") {
          this.componentName = "TransactionRate";
        } else if (this.componentName == "PurchasePrice") {
          this.componentName = "TransactionDate";
        } else if (this.componentName == "TaxCalculation") {
          this.componentName = "PurchasePrice"; //End of route
        }
      }

      //NonResidential Route
      if (this.inputs.transactionType == "NonResidential") {
        if (this.componentName == "PropertyType") {
          this.componentName = "TransactionType";
        } else if (this.componentName == "CalculationDate") {
          this.componentName = "PropertyType";
        } else if (
          this.componentName == "TransactionDate" &&
          this.inputs.propertyType != "NewLease"
        ) {
          this.componentName = "PropertyType";
        } else if (
          this.componentName == "TransactionDate" &&
          this.inputs.propertyType == "NewLease"
        ) {
          this.componentName = "CalculationDate";
        } else if (this.componentName == "PurchasePrice") {
          this.componentName = "TransactionDate";
        } else if (
          this.componentName == "TaxCalculation" &&
          this.inputs.propertyType != "NewLease"
        ) {
          this.componentName = "PurchasePrice"; //End of Freehold route & End of ExistingLease route
        } else if (this.componentName == "LeaseTerm") {
          this.componentName = "TransactionDate";
        } else if (this.componentName == "RentInformation") {
          this.componentName = "LeaseTerm";
        } else if (this.componentName == "TotalPremium") {
          this.componentName = "RentInformation";
        } else if (
          this.componentName == "TaxCalculation" &&
          this.inputs.propertyType == "NewLease"
        ) {
          this.componentName = "TotalPremium"; //End of NewLease route
        }
      }
    },
    nextStage: function(localInputs) {
      this.inputs = localInputs;

      //Residential Route
      if (this.inputs.transactionType == "Residential") {
        if (this.componentName == "TransactionType") {
          this.componentName = "TransactionRate";
        } else if (this.componentName == "TransactionRate") {
          this.componentName = "TransactionDate";
        } else if (this.componentName == "TransactionDate") {
          this.componentName = "PurchasePrice";
        } else if (this.componentName == "PurchasePrice") {
          this.componentName = "TaxCalculation"; //End of route
        }
      }

      //NonResidential Route
      if (this.inputs.transactionType == "NonResidential") {
        if (this.componentName == "TransactionType") {
          this.componentName = "PropertyType";
        } else if (
          this.componentName == "PropertyType" &&
          this.inputs.propertyType != "NewLease"
        ) {
          this.componentName = "TransactionDate";
        } else if (
          this.componentName == "PropertyType" &&
          this.inputs.propertyType == "NewLease"
        ) {
          this.componentName = "CalculationDate";
        } else if (this.componentName == "CalculationDate") {
          this.componentName = "TransactionDate";
        } else if (
          this.componentName == "TransactionDate" &&
          this.inputs.propertyType != "NewLease"
        ) {
          this.componentName = "PurchasePrice";
        } else if (this.componentName == "PurchasePrice") {
          this.componentName = "TaxCalculation"; //End of Freehold route & End of ExistingLease route
        } else if (
          this.componentName == "TransactionDate" &&
          this.inputs.propertyType == "NewLease"
        ) {
          this.componentName = "LeaseTerm";
        } else if (this.componentName == "LeaseTerm") {
          this.componentName = "RentInformation";
        } else if (this.componentName == "RentInformation") {
          this.componentName = "TotalPremium";
        } else if (this.componentName == "TotalPremium") {
          this.componentName = "TaxCalculation"; //End of NewLease route
        }
      }
    },
    goToStage: function(inputState) {
      this.inputs = inputState.localInputs;
      this.componentName = inputState.componentName;
    },
    reset: function() {
      this.inputs = {
        transactionType: null,
        transactionRate: null,
        propertyType: null,
        transactionDate: null,
        purchasePrice: null,
        leaseStartDate: null,
        leaseEndDate: null,
        calculationDate: null,
        rentYearOne: null,
        rentYearTwo: null,
        rentYearThree: null,
        rentYearFour: null,
        rentYearFive: null,
        relevantRent: null,
        highestRent: null
      };
      this.componentName = "TransactionType";
    },
    getDate: function(stringDate) {
      return new Date(
        stringDate.split("/")[1] +
          "/" +
          stringDate.split("/")[0] +
          "/" +
          stringDate.split("/")[2]
      );
    },
    scrollUp: function() {
      if (window.pageYOffset > 378) {
        this.$scrollTo("#questionCard", 500, {});
      }
    }
  },
  computed: {
    leaseDuration: function() {
      if (
        this.inputs.transactionDate != null &&
        this.inputs.leaseStartDate != null &&
        this.inputs.leaseEndDate != null
      ) {
        //Convert transactionDate, leaseStartDate and leaseEndDate into Date objects
        var transactionDate = this.getDate(this.inputs.transactionDate);
        var leaseStartDate = this.getDate(this.inputs.leaseStartDate);
        var leaseEndDate = this.getDate(this.inputs.leaseEndDate);

        var chosenStartDate = "";

        if (this.inputs.calculationDate == "EffectiveDate") {
          chosenStartDate = transactionDate;
        } else if (this.inputs.calculationDate == "LeaseStartDate") {
          chosenStartDate = leaseStartDate;
        }

        var fullYearEndString =
          chosenStartDate.getDate() +
          "/" +
          (chosenStartDate.getMonth() + 1) + // +1 because January = 0
          "/" +
          leaseEndDate.getFullYear();
        var fullYearEnd = this.getDate(fullYearEndString);

        fullYearEnd.setDate(fullYearEnd.getDate() - 1);

        // For cases where the start date is 1 January (and end date is 31 December), we got issues.
        // It'll work out the full year end as being a whole year before the actual year end.
        // The above works mostly, but we need to solve for the full year end being one whole year behind the actual end date
        // If it is, we can just add one year.
        if (
          leaseEndDate.getDate() == fullYearEnd.getDate() &&
          leaseEndDate.getMonth() == fullYearEnd.getMonth() &&
          leaseEndDate.getFullYear() - fullYearEnd.getFullYear() == 1
        ) {
          fullYearEnd.setFullYear(fullYearEnd.getFullYear() + 1);
        }

        if (leaseEndDate - fullYearEnd < 0) {
          // If the leaseEndDate - fullYearEnd is less than 0
          // Then FYE = FYE - one whole year
          fullYearEnd.setFullYear(fullYearEnd.getFullYear() - 1);
        }

        var diffFullYears =
          fullYearEnd.getFullYear() - chosenStartDate.getFullYear();
        // If the start date was the 1 January, we need to add an extra year to the calculated year duration
        if (chosenStartDate.getDate() == 1 && chosenStartDate.getMonth() == 0) {
          diffFullYears++;
        }

        const utcFullYearEnd = Date.UTC(
          fullYearEnd.getFullYear(),
          fullYearEnd.getMonth(),
          fullYearEnd.getDate()
        );
        const utcLeaseEndDate = Date.UTC(
          leaseEndDate.getFullYear(),
          leaseEndDate.getMonth(),
          leaseEndDate.getDate()
        );

        //Calculate the diff between the 2 dates
        var diffRemainingDays = Math.ceil(
          (utcLeaseEndDate - utcFullYearEnd) / (1000 * 60 * 60 * 24)
        );

        var diffYearsSpanned = diffFullYears;
        if (diffRemainingDays > 0) {
          diffYearsSpanned++;
        }

        var leaseDuration = {
          fullYears: diffFullYears,
          remainingDays: diffRemainingDays,
          yearsSpanned: diffYearsSpanned,
          fullYearEnd: fullYearEnd
        };

        return leaseDuration;
      } else {
        return null;
      }
    },
    leaseDisplay: function() {
      if (this.leaseDuration != null) {
        if (
          this.leaseDuration.fullYears == 1 &&
          this.leaseDuration.remainingDays == 1
        ) {
          return (
            this.leaseDuration.fullYears +
            " " +
            this.text.taxCalculation.answerSummary.year +
            " " +
            this.leaseDuration.remainingDays +
            " " +
            this.text.taxCalculation.answerSummary.day
          );
        } else if (
          this.leaseDuration.fullYears == 1 &&
          this.leaseDuration.remainingDays != 1
        ) {
          return (
            this.leaseDuration.fullYears +
            " " +
            this.text.taxCalculation.answerSummary.year +
            " " +
            this.leaseDuration.remainingDays +
            " " +
            this.text.taxCalculation.answerSummary.days
          );
        } else if (
          this.leaseDuration.fullYears != 1 &&
          this.leaseDuration.remainingDays == 1
        ) {
          return (
            this.leaseDuration.fullYears +
            " " +
            this.text.taxCalculation.answerSummary.years +
            " " +
            this.leaseDuration.remainingDays +
            " " +
            this.text.taxCalculation.answerSummary.day
          );
        } else if (
          this.leaseDuration.fullYears != 1 &&
          this.leaseDuration.remainingDays != 1
        ) {
          return (
            this.leaseDuration.fullYears +
            " " +
            this.text.taxCalculation.answerSummary.years +
            " " +
            this.leaseDuration.remainingDays +
            " " +
            this.text.taxCalculation.answerSummary.days
          );
        } else {
          return null;
        }
      } else {
        return null;
      }
    }
  },
  mounted() {
    this.getText();
    this.setStage();
  },
  components: {
    TransactionType,
    TransactionRate,
    PropertyType,
    TransactionDate,
    PurchasePrice,
    TotalPremium,
    LeaseTerm,
    CalculationDate,
    RentInformation,
    TaxCalculation
  }
};
</script>

<style scoped>
.fade-enter-active {
  transition: opacity 0.25s ease;
}

.fade-leave-active {
  transition: opacity 0s ease;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-to,
.fade-leave {
  opacity: 1;
}
</style>
