<template>
  <v-container class="pt-0 px-5 pb-10 mb-10">
    <!--Heading-->
    <v-row no-gutters class="pb-6">
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <v-divider
          role="presentation"
          style="height: 6px;
          max-height: 6px;
          background: #e5e5e5;
          border: none;"
        ></v-divider>

        <a ref="topofpage" href="#" tabindex="-1"></a>

        <transition name="translate" mode="out-in">
          <wra-subheader class="mt-8" :key="startText.headingText">
            {{ startText.beforeHeadingText }}
          </wra-subheader>
        </transition>
        <transition name="translate" mode="out-in">
          <h1 :key="startText.headingText">
            {{ startText.headingText }}
          </h1>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <transition name="translate" mode="out-in">
          <div class="start-text" :key="startText.headingText">
            <router-link :to="{ name: 'lttcalculator' }">
              {{ startText.lttCalculator.linkText }}
            </router-link>
            <br />
            {{ startText.lttCalculator.text }}
          </div>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <v-row no-gutters class="mt-4">
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <transition name="translate" mode="out-in">
          <div class="start-text" :key="startText.headingText">
            <router-link :to="{ name: 'ratechecker' }">
              {{ startText.rateChecker.linkText }}
            </router-link>
            <br />
            {{ startText.rateChecker.text }}
          </div>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>
  </v-container>
</template>

<script>
const axios = require("axios");

export default {
  props: ["lang"],

  data: () => ({
    startText: {
      rateChecker: {},
      lttCalculator: {}
    }
  }),

  watch: {
    lang: {
      handler() {
        this.getText();
      }
    }
  },

  methods: {
    getText: function() {
      axios.get("./text/" + this.lang + "/start.json").then((response) => {
        this.startText = response.data.startText;
        document.title = this.startText.documentTitleText;
        this.$refs.topofpage.focus();
      });
    }
  },

  mounted() {
    this.getText();
  }
};
</script>

<style>
.start-text {
  font-size: 18px;
}
</style>
