<template>
  <v-container
    class="pt-0 pb-3"
    :class="{
      'px-0': $vuetify.breakpoint.smAndDown
    }"
  >
    <!--Heading-->
    <v-row
      no-gutters
      class="pb-6"
      :class="{
        'px-5': $vuetify.breakpoint.smAndDown
      }"
    >
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <v-divider
          role="presentation"
          style="height: 6px;
          max-height: 6px;
          background: #e5e5e5;
          border: none;"
        ></v-divider>
        <wra-subheader class="mt-8">
          {{ rateCheckerText.beforeHeadingText }}
        </wra-subheader>
        <h1>
          {{ rateCheckerText.headingText }}
        </h1>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <a ref="topofpage" href="#" tabindex="-1"></a>

    <!--Card-->
    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12" class="minimum-height">
        <component
          :is="componentName"
          v-if="loaded"
          :lang="lang"
          :stage="stageContent"
          :text="rateCheckerText"
          :previous-stages="previousStages"
          v-on:next="nextStage"
          v-on:previous="previousStage"
          v-on:restart="firstStage"
        ></component>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <!--Help & Guidance-->
    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <transition name="translate" mode="out-in">
        <v-col
          lg="8"
          cols="12"
          class="pt-6 pb-3 hidden-print"
          :class="{
            'px-5': $vuetify.breakpoint.smAndDown
          }"
          :key="rateCheckerText.beforeHeadingText"
        >
          <wra-button
            outlined
            class="my-8"
            :href="rateCheckerText.helpGuidance.helpLink"
            target="_blank"
            rel="noreferrer"
            :aria-label="
              rateCheckerText.helpGuidance.helpText +
                '(' +
                rateCheckerText.helpGuidance.newTab +
                ')'
            "
            id="helpLink"
          >
            {{ rateCheckerText.helpGuidance.helpText }}
            <wra-icon right size="24" class="mx-3">mdi-open-in-new</wra-icon>
          </wra-button>
        </v-col>
      </transition>
      <v-col lg="2" cols="0"></v-col>
    </v-row>
  </v-container>
</template>

<script>
import CheckerResult from "../components/rate-checker/CheckerResult.vue";
import CheckerQuestion from "../components/rate-checker/CheckerQuestion.vue";

const axios = require("axios");

export default {
  props: ["lang"],

  data: () => ({
    componentName: "",
    loaded: false,
    rateCheckerText: {
      helpGuidance: {}
    },
    stage: {
      StageEnglish: { Answers: [] },
      StageCymraeg: { Answers: [] },
      PreviousStageID: 1,
      StageID: 1
    },
    stageContent: {
      Answers: [],
      selectedAnswer: {}
    },
    previousStages: [],
    isResult: false
  }),

  watch: {
    lang: {
      handler() {
        this.getText();
        this.setStageContent();
      }
    },
    stage: {
      handler() {
        this.setStageContent();
        if (
          this.stageContent.StageTitle == "Result" ||
          this.stageContent.StageTitle == "Canlyniad"
        ) {
          this.isResult = true;
          this.componentName = "CheckerResult";
        } else {
          this.isResult = false;
          this.componentName = "CheckerQuestion";
        }
      }
    }
  },

  methods: {
    setStageContent: function() {
      if (this.lang == "english") {
        this.stageContent = this.stage.StageEnglish;
        this.stageContent.selectedAnswer = this.stage.selectedAnswer;
        if (this.previousStages) {
          this.previousStages.forEach(function(previousStage) {
            var basedOnAnswer = previousStage.StageEnglish.Answers.filter(
              (answer) =>
                answer.AnswerID == previousStage.selectedAnswer.AnswerID
            );
            previousStage.AnswerText = basedOnAnswer[0].AnswerText;
            previousStage.selectedAnswer = basedOnAnswer[0];
          });
        }
      } else {
        this.stageContent = this.stage.StageCymraeg;
        this.stageContent.selectedAnswer = this.stage.selectedAnswer;
        if (this.previousStages) {
          this.previousStages.forEach(function(previousStage) {
            var basedOnAnswer = previousStage.StageCymraeg.Answers.filter(
              (answer) =>
                answer.AnswerID == previousStage.selectedAnswer.AnswerID
            );
            previousStage.AnswerText = basedOnAnswer[0].AnswerText;
            previousStage.selectedAnswer = basedOnAnswer[0];
          });
        }
      }
      document.title = this.stageContent.StageTitle;
      this.$ga.page("/" + this.stageContent.StageTitle);
      this.$refs.topofpage.focus();
    },
    getText: function() {
      axios
        .get("./text/" + this.lang + "/ratechecker.json")
        .then((response) => {
          this.rateCheckerText = response.data.rateCheckerText;
        });
    },
    getStage: function(StageID) {
      this.loaded = false;
      axios.get("./stages/" + StageID + ".txt").then((response) => {
        this.selectedAnswer = {};
        this.stage = response.data;
        this.loaded = true;
      });
    },
    nextStage: function(selectedAnswer) {
      this.stage.selectedAnswer = selectedAnswer;
      this.previousStages.push(this.stage);
      this.getStage(selectedAnswer.RouteStageID);
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event(
          "Rate Checker",
          this.stageContent.StageTitle,
          this.selectedAnswer.AnswerText,
          this.selectedAnswer.AnswerID
        );
      }
    },
    previousStage: function() {
      if (this.stage.StageID != 1) {
        this.stage = this.previousStages.pop();
      } else {
        this.$router.push("/");
      }
    },
    firstStage: function() {
      this.stage = this.previousStages.shift();
      this.previousStages = [];
    }
  },
  mounted() {
    this.getText();
    this.getStage(1);
  },
  components: {
    CheckerResult,
    CheckerQuestion
  }
};
</script>

<style></style>
