<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
    v-if="this.totalTax != null"
  >
    <transition name="translate" mode="out-in">
      <div :key="text.taxCalculation.title">
        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.taxCalculation.title"></h2>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.taxCalculation.description"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- v-if Route 1 or 2 -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'Residential' ||
              localInputs.propertyType == 'Freehold' ||
              localInputs.propertyType == 'ExistingLease'
          "
        >
          <wra-card-text class="calc-result">
            <v-row class="ma-0">
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalTaxDue }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatCurrency(totalTax) }}
                </div>
              </v-col>
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.effectiveDate }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  {{ localInputs.transactionDate }}
                </div>
              </v-col>
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.purchasePrice }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ localInputs.purchasePrice }}
                </div>
              </v-col>
            </v-row>
          </wra-card-text>
        </wra-card-text>

        <!-- v-if Route 3 -->
        <wra-card-text class="pa-4" v-else>
          <wra-card-text class="calc-result">
            <v-row class="ma-0">
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalTaxDue }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatCurrency(totalTax) }}
                </div>
              </v-col>
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.effectiveDate }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  {{ localInputs.transactionDate }}
                </div>
              </v-col>
            </v-row>
            <v-row class="ma-0">
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalPremium }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ localInputs.purchasePrice }}
                </div>
              </v-col>
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalNpv }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatUseGrouping(npv) }}
                </div>
              </v-col>
            </v-row>
          </wra-card-text>
        </wra-card-text>

        <!-- 'Summary of your answers' expansion panel -->
        <wra-card-text class="pa-4">
          <wra-expansion-panels flat v-model="summaryPanel">
            <wra-expansion-panel>
              <wra-expansion-panel-header>
                {{ text.taxCalculation.answerSummary.title }}
              </wra-expansion-panel-header>

              <wra-expansion-panel-content class="pt-0">
                <wra-simple-table>
                  <template v-slot:default>
                    <caption>
                      {{
                        text.taxCalculation.answerSummary.title
                      }}
                    </caption>
                    <tbody>
                      <!--TransactionType-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionType
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionType == 'Residential'"
                        >
                          {{ text.transactionType.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionType == 'NonResidential'"
                        >
                          {{ text.transactionType.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionType')"
                            id="transactionTypeEdit"
                          >
                            {{ text.taxCalculation.answerSummary.edit }}
                          </wra-button>
                        </td>
                      </tr>
                      <!--TransactionRate-->
                      <tr v-if="localInputs.transactionType == 'Residential'">
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionRate
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionRate == 'HigherRates'"
                        >
                          {{ text.transactionRate.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionRate == 'MainRates'"
                        >
                          {{ text.transactionRate.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionRate')"
                            id="transactionRateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--PropertyType-->
                      <tr
                        v-if="localInputs.transactionType == 'NonResidential'"
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.propertyType }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.propertyType == 'Freehold'"
                        >
                          {{ text.propertyType.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.propertyType == 'NewLease'"
                        >
                          {{ text.propertyType.answers[1].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.propertyType == 'ExistingLease'"
                        >
                          {{ text.propertyType.answers[2].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('PropertyType')"
                            id="propertyTypeEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--CalculationDate-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.calculationDate
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.calculationDate == 'EffectiveDate'"
                        >
                          {{ text.calculationDate.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.calculationDate == 'LeaseStartDate'"
                        >
                          {{ text.calculationDate.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('CalculationDate')"
                            id="calculationDateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--TransactionDate-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionDate
                          }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.transactionDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionDate')"
                            id="transactionDateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>

                      <!--LeaseTerm-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.leaseStartDate }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.leaseStartDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('LeaseTerm')"
                            id="leaseStartEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.leaseEndDate }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.leaseEndDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('LeaseTerm')"
                            id="leaseEndEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.leaseDuration }}
                        </td>
                        <td class="text-right">
                          {{ leaseDisplay }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('LeaseTerm')"
                            id="leaseTermEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--RentInformation-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearOne }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearOne }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearOneEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearTwo != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearTwo }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearTwo }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearTwoEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearThree != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearThree }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearThree }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearThreeEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearFour != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearFour }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearFour }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearFourEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearFive != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearFive }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearFive }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearFiveEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.relevantRent }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.relevantRent }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="relevantRentEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.highestRent != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.highestRent }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.highestRent }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="highestRentEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--PurchasePrice-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'Residential' ||
                            (localInputs.transactionType == 'NonResidential' &&
                              localInputs.propertyType != 'NewLease')
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.purchasePrice }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.purchasePrice }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('PurchasePrice')"
                            id="purchasePriceEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--TotalPremium-->
                      <tr v-else>
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.totalPremium }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.purchasePrice }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TotalPremium')"
                            id="totalPremiumEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </wra-simple-table>
              </wra-expansion-panel-content>
            </wra-expansion-panel>
          </wra-expansion-panels>
        </wra-card-text>

        <!-- Detailed calculation -->
        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.taxCalculation.calcBreakdown.title"></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.description"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- LTT on the premium -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.taxCalculation.calcBreakdown.premiumTitle"></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.premiumDescr"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- Purchase Price Bands Table (LTT on the premium) -->
        <wra-card-text class="pt-0 px-0 pb-4">
          <wra-data-table
            :headers="basedOnPremiumHeaders"
            :items="bands"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:item.amount="{ item }">
              {{ formatUseGrouping(item.amount) }}
            </template>

            <template v-slot:item.tax="{ item }">
              {{ formatCurrency(item.tax) }}
            </template>

            <template v-slot:foot>
              <tfoot>
                <tr class="calc-breakdown-total">
                  <td
                    colspan="4"
                    class="text-right"
                    v-if="
                      localInputs.transactionType == 'Residential' ||
                        (localInputs.transactionType == 'NonResidential' &&
                          localInputs.propertyType != 'NewLease')
                    "
                  >
                    {{ text.taxCalculation.calcBreakdown.totalTaxDue }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnPremium) }}
                  </td>
                  <td colspan="4" class="text-right" v-else>
                    {{ text.taxCalculation.calcBreakdown.basedOnPremium }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnPremium) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <!-- LTT on the net present value (NPV) of rent -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.taxCalculation.calcBreakdown.npvTitle"></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.npvDescr"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- Rent Bands/NPV Table (LTT on the NPV of rent) -->
        <wra-card-text
          class="pt-0 px-0 pb-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-data-table
            :headers="basedOnNpvHeaders"
            :items="npvBands"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:item.amount="{ item }">
              {{ formatUseGrouping(item.amount) }}
            </template>

            <template v-slot:item.tax="{ item }">
              {{ formatCurrency(item.tax) }}
            </template>

            <template v-slot:foot>
              <tfoot>
                <tr class="calc-breakdown-total">
                  <td colspan="4" class="text-right">
                    {{ text.taxCalculation.calcBreakdown.basedOnNpv }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnNpv) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <!-- Tax chargeable -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2
              v-html="text.taxCalculation.calcBreakdown.subTotalCalcTitle"
            ></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.subTotalCalcDescr"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- Tax Chargeable Table -->
        <wra-card-text
          class="pt-0 px-0 pb-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-data-table
            :headers="taxChargeableHeaders"
            :items="taxChargeableItems"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:foot>
              <tfoot>
                <tr class="calc-breakdown-total">
                  <td colspan="3" class="text-right">
                    {{ text.taxCalculation.calcBreakdown.totalTaxDue }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(totalTax) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <p>
            {{ text.taxCalculation.calcBreakdown.payLtt.textP1 }}
            <a
              :href="text.taxCalculation.calcBreakdown.payLtt.link"
              target="_blank"
              rel="noreferrer"
              :aria-label="
                text.taxCalculation.calcBreakdown.payLtt.linkText +
                  ' (' +
                  text.taxCalculation.calcBreakdown.payLtt.newTab +
                  ')'
              "
              >{{ text.taxCalculation.calcBreakdown.payLtt.linkText }}</a
            >
            {{ text.taxCalculation.calcBreakdown.payLtt.textP2 }}
          </p>
        </wra-card-text>

        <wra-card-text class="hidden-print">
          <wra-button outlined @click="printClicked()">
            {{ text.taxCalculation.print }}
          </wra-button>
        </wra-card-text>

        <wra-card-text class="hidden-print">
          <p>
            <a
              :href="text.taxCalculation.feedback.link"
              target="_blank"
              rel="noreferrer"
              :aria-label="
                text.taxCalculation.feedback.text +
                  ' (' +
                  text.taxCalculation.feedback.newTab +
                  ')'
              "
              >{{ text.taxCalculation.feedback.text }}</a
            >
            <wra-icon class="ml-1" color="#1F1F1F">mdi-open-in-new</wra-icon>
          </p>
        </wra-card-text>

        <wra-card-actions class="pt-4 px-4 pb-2 hidden-print">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="firstStage()"
                id="restartButton"
              >
                {{ text.restart }}
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButton"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </div>
    </transition>
  </wra-card>
</template>

<script>
const axios = require("axios");
export default {
  props: ["text", "inputs", "leaseDuration", "leaseDisplay"],

  data: () => ({
    localInputs: {},
    rates: null,
    relevantRent: null,
    bands: [],
    npvBands: [],
    totalTax: null,
    taxBasedOnPremium: null,
    taxBasedOnNpv: null,
    npv: null,
    summaryPanel: []
  }),
  watch: {
    text: {
      deep: true,
      handler() {
        this.describeBands();
        if (
          this.localInputs.transactionType == "NonResidential" &&
          this.localInputs.propertyType == "NewLease"
        ) {
          this.describeNpvBands();
        }
      }
    },
    summaryPanel: function(newValue) {
      var label;

      if (newValue == null) {
        label = "Close";
      } else if (newValue == 0) {
        label = "Open";
      }
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Summary", label);
      }
    }
  },
  computed: {
    basedOnPremiumHeaders: function() {
      return [
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.priceBands +
            " (£)",
          value: "description"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.percentageRate +
            " (%)",
          value: "rate",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.amount + " (£)",
          value: "amount",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.taxDue + " (£)",
          value: "tax",
          align: "right"
        }
      ];
    },
    basedOnNpvHeaders: function() {
      return [
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.rentBands +
            " (£)",
          value: "description"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.percentageRate +
            " (%)",
          value: "rate",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.amount + " (£)",
          value: "amount",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.taxDue + " (£)",
          value: "tax",
          align: "right"
        }
      ];
    },
    taxChargeableHeaders: function() {
      return [
        {
          text: this.text.taxCalculation.calcBreakdown.tableHeaders.type,
          value: "type"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.totalAmount +
            " (£)",
          value: "totalAmount",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.taxDue + " (£)",
          value: "taxDue",
          align: "right"
        }
      ];
    },
    taxChargeableItems: function() {
      return [
        {
          type: this.text.taxCalculation.calcBreakdown.rowDescriptions
            .basedOnPremium,
          totalAmount: this.localInputs.purchasePrice,
          taxDue: this.formatCurrency(this.taxBasedOnPremium)
        },
        {
          type: this.text.taxCalculation.calcBreakdown.rowDescriptions
            .basedOnNpv,
          totalAmount: this.formatUseGrouping(this.npv),
          taxDue: this.formatCurrency(this.taxBasedOnNpv)
        }
      ];
    }
  },
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Tax calculation", "Back");
      }
      this.$emit("previous", this.localInputs);
    },
    firstStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Start again");
      }
      this.$emit("reset");
    },
    formatCurrency: function(number) {
      return Intl.NumberFormat("en-GB", {
        useGrouping: true,
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      }).format(number);
    },
    formatUseGrouping: function(number) {
      return Intl.NumberFormat("en-GB", {
        useGrouping: true,
        minimumFractionDigits: 0,
        maximumFractionDigits: 0
      }).format(number);
    },
    calculate: function() {
      var bandSeries = {};

      var start = this.localInputs.transactionDate;

      if (this.localInputs.transactionType == "Residential") {
        if (this.localInputs.transactionRate == "MainRates") {
          bandSeries = this.rates.residential; //Main residential tax rates
        } else if (this.localInputs.transactionRate == "HigherRates") {
          bandSeries = this.rates.higher; //Higher residential tax rates
        }
      } else if (this.localInputs.transactionType == "NonResidential") {
        bandSeries = this.rates.nonResidential; //Non-residential property tax rates

        if (this.localInputs.propertyType == "NewLease") {
          //Find applicable relevant rent
          var aptRelevantRent = this.relevantRent.find(
            (element) =>
              this.getDate(element.startDate) <= this.getDate(start) &&
              this.getDate(element.endDate) >= this.getDate(start)
          );
          //Find applicable relevant rent rates
          var aptRates = bandSeries.find(
            (element) =>
              this.getDate(element.startDate) <= this.getDate(start) &&
              this.getDate(element.endDate) >= this.getDate(start)
          );

          if (
            this.localInputs.relevantRent.replaceAll(",", "") >=
            aptRelevantRent.value
          ) {
            //Remove first band
            aptRates.bands.shift();

            //Remove second band, modify (set min to 0), put back in
            var band = aptRates.bands.shift();
            band.min = 1;
            aptRates.bands.unshift(band);
          }
        }
      }

      var relevantRates = bandSeries.find(
        (element) =>
          this.getDate(element.startDate) <= this.getDate(start) &&
          this.getDate(element.endDate) >= this.getDate(start)
      );

      var relevantNpvRates = this.rates.netPresentValue.find(
        (element) =>
          this.getDate(element.startDate) <= this.getDate(start) &&
          this.getDate(element.endDate) >= this.getDate(start)
      );

      this.bands = relevantRates.bands;
      this.npvBands = relevantNpvRates.bands;

      var purchasePrice = this.localInputs.purchasePrice.replaceAll(",", ""); //Remove commas from purchasePrice

      if (this.localInputs.propertyType == "NewLease") {
        var relevantRent = this.localInputs.relevantRent.replaceAll(",", "");
        //If purchasePrice is less than 40,000 AND relevantRent is less than the threshold
        //Only then do you set purchasePrice to 0
        //If relevantRent >= the threshold, you must pay tax on the premium
        if (purchasePrice < 40000 && relevantRent < aptRelevantRent.value) {
          purchasePrice = 0;
        }
      } else {
        if (purchasePrice < 40000) {
          purchasePrice = 0;
        }
      }

      var taxBasedOnPremium = 0;

      this.describeBands();

      //taxBasedOnPremium
      this.bands.forEach(function(band) {
        if (purchasePrice < band.min) {
          band.amount = 0;
        } else if (band.max > purchasePrice) {
          band.amount = purchasePrice - band.min + 1;
        } else {
          band.amount = band.max - band.min + 1;
        }

        band.tax = Math.floor(band.amount * band.rate) / 100;

        taxBasedOnPremium += band.tax;
      }, this);

      this.taxBasedOnPremium = taxBasedOnPremium;
    },
    describeBands: function() {
      //taxBasedOnPremium
      this.bands.forEach(function(band, index, bands) {
        if (index == 0) {
          //First row
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.upTo +
            " " +
            this.formatUseGrouping(band.max);
        } else if (index == bands.length - 1) {
          //Last row
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.above +
            " " +
            this.formatUseGrouping(band.min - 1);
        } else {
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.above +
            " " +
            this.formatUseGrouping(band.min - 1) +
            " " +
            this.text.taxCalculation.calcBreakdown.rowDescriptions.andUpTo +
            " " +
            this.formatUseGrouping(band.max);
        }
      }, this);
    },
    calculateNpv: function() {
      var rentEachYear = [];
      var taxBasedOnNpv = 0;

      //Add rent values to rentEachYear - convert to Numbers & remove thousands separators
      rentEachYear.push(
        Number(this.localInputs.rentYearOne.replaceAll(",", ""))
      );

      if (
        this.localInputs.rentYearTwo != null &&
        this.leaseDuration.yearsSpanned >= 2
      ) {
        rentEachYear.push(
          Number(this.localInputs.rentYearTwo.replaceAll(",", ""))
        );
      }
      if (
        this.localInputs.rentYearThree != null &&
        this.leaseDuration.yearsSpanned >= 3
      ) {
        rentEachYear.push(
          Number(this.localInputs.rentYearThree.replaceAll(",", ""))
        );
      }
      if (
        this.localInputs.rentYearFour != null &&
        this.leaseDuration.yearsSpanned >= 4
      ) {
        rentEachYear.push(
          Number(this.localInputs.rentYearFour.replaceAll(",", ""))
        );
      }
      if (
        this.localInputs.rentYearFive != null &&
        this.leaseDuration.yearsSpanned >= 5
      ) {
        rentEachYear.push(
          Number(this.localInputs.rentYearFive.replaceAll(",", ""))
        );
      }

      //Remove 5 years from yearsInLease (because we already have those values)
      //Loop through yearsInLease, adding Highest Rent figure to array each time
      var yearsInLease = this.leaseDuration.fullYears - 5;
      for (let i = 0; i < yearsInLease; i++) {
        rentEachYear.push(
          Number(this.localInputs.highestRent.replaceAll(",", ""))
        );
      }

      //Work out how much of the annual Highest Rent figure is applicable to the remaining day/s
      //Add to rentEachYear array
      if (
        this.leaseDuration.remainingDays > 0 &&
        this.localInputs.highestRent != null &&
        this.leaseDuration.fullYears >= 5
      ) {
        //Calculate whether the last year of the lease is 365 or 366 days
        const utcLastFullYear = Date.UTC(
          this.leaseDuration.fullYearEnd.getFullYear(),
          this.leaseDuration.fullYearEnd.getMonth(),
          this.leaseDuration.fullYearEnd.getDate()
        );
        const utcYearAfterLast = Date.UTC(
          this.leaseDuration.fullYearEnd.getFullYear() + 1,
          this.leaseDuration.fullYearEnd.getMonth(),
          this.leaseDuration.fullYearEnd.getDate()
        );

        //Calculate the diff between the 2 dates
        var lastYearDays = Math.ceil(
          (utcYearAfterLast - utcLastFullYear) / (1000 * 60 * 60 * 24)
        );

        //Divide the remaining days by the number of days in the last year of the lease (365 or 366)
        var ratio = this.leaseDuration.remainingDays / lastYearDays;

        rentEachYear.push(
          this.localInputs.highestRent.replaceAll(",", "") * ratio
        );
      }

      var npv = 0;

      //Loop through rentEachYear array and add up the figures
      rentEachYear.forEach((item, index) => {
        //@RentYear1/(power((1+@NPVRate),1))
        var npvEachYear = item / (1 + 0.035) ** (index + 1);
        npv = npv + npvEachYear;
      });

      this.npv = Math.floor(npv);

      this.describeNpvBands();

      //taxBasedOnNpv
      this.npvBands.forEach(function(band) {
        if (this.npv < band.min) {
          band.amount = 0;
        } else if (band.max > this.npv) {
          band.amount = this.npv - band.min + 1;
        } else {
          band.amount = band.max - band.min + 1;
        }

        band.tax = Math.floor(band.amount * band.rate) / 100;

        taxBasedOnNpv += band.tax;
      }, this);

      this.taxBasedOnNpv = taxBasedOnNpv;
    },
    describeNpvBands: function() {
      //taxBasedOnNpv
      this.npvBands.forEach(function(band, index, npvBands) {
        if (index == 0) {
          //First row
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.upTo +
            " " +
            this.formatUseGrouping(band.max);
        } else if (index == npvBands.length - 1) {
          //Last row
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.above +
            " " +
            this.formatUseGrouping(band.min - 1);
        } else {
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.above +
            " " +
            this.formatUseGrouping(band.min - 1) +
            " " +
            this.text.taxCalculation.calcBreakdown.rowDescriptions.andUpTo +
            " " +
            this.formatUseGrouping(band.max);
        }
      }, this);
    },
    calculateTotal: function() {
      this.totalTax = this.taxBasedOnPremium + this.taxBasedOnNpv;
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event(
          "LTT Calculator",
          "Tax calculated",
          this.formatCurrency(this.totalTax),
          Math.round(this.totalTax)
        );
      }
    },
    getDate: function(stringDate) {
      return new Date(
        stringDate.split("/")[1] +
          "/" +
          stringDate.split("/")[0] +
          "/" +
          stringDate.split("/")[2]
      );
    },
    editStage: function(stage) {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Edit", stage);
      }
      this.$emit("goToStage", {
        localInputs: this.localInputs,
        componentName: stage
      });
    },
    printClicked: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Print or Save");
      }
      window.print();
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.totalTax = null; // reset the tax amount to null before calculating (or recalcualting on second visit)
    axios.get("./rates/rates.json").then((response) => {
      this.rates = response.data.rates;
      this.relevantRent = response.data.relevantRent;
      this.calculate();
      if (
        this.localInputs.transactionType == "NonResidential" &&
        this.localInputs.propertyType == "NewLease"
      ) {
        this.calculateNpv();
      }
      this.calculateTotal();
      this.$nextTick(() => {
        this.$emit("scrollUp");
      });
    });
  }
};
</script>
