<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
  >
    <a ref="topofcard" href="#" tabindex="-1"></a>

    <!--Validation -- Question Page Only-->
    <wra-card-text v-if="showValidation" class="pa-4">
      <wra-validation-tooltip type="wra-error" role="status">
        {{ text.validationText }}
      </wra-validation-tooltip>
    </wra-card-text>

    <wra-card-text class="pa-4">
      <wra-card-title class="pt-0 px-0 pb-4">
        <h2 v-html="stage.StageTitle"></h2>
      </wra-card-title>
      <wra-card-text v-if="stage.StageText != ''" class="pt-0 px-0 pb-8">
        <p v-html="stage.StageText"></p>
      </wra-card-text>

      <wra-radio-group v-model="stage.selectedAnswer">
        <template v-slot:label>
          {{ stage.StageTitle }}
        </template>
        <wra-radio
          v-for="answer in stage.Answers"
          :key="answer.AnswerID"
          :label="answer.AnswerText"
          :value="answer"
        >
          <template v-slot:label>
            <p v-html="answer.AnswerText"></p>
            <p
              class="further-information-text"
              v-html="answer.FurtherInformation"
            ></p>
          </template>
        </wra-radio>
      </wra-radio-group>
    </wra-card-text>

    <!--'Need help?' expansion panel (FooterText)-->
    <wra-card-text v-if="stage.FooterText != ''" class="pt-0 px-4 pb-8">
      <wra-expansion-panels flat>
        <wra-expansion-panel>
          <wra-expansion-panel-header>
            {{ text.needHelp }}
          </wra-expansion-panel-header>
          <wra-expansion-panel-content>
            <p v-html="stage.FooterText"></p>
          </wra-expansion-panel-content>
        </wra-expansion-panel>
      </wra-expansion-panels>
    </wra-card-text>

    <wra-card-actions class="py-2 px-4">
      <v-row>
        <v-col cols="12" md="2" order="first" order-md="last">
          <wra-button dark class="mb-2" @click="nextStage()">
            {{ text.nextButton }}
            <wra-icon alt="" right large>navigate_next</wra-icon>
          </wra-button>
        </v-col>
        <v-col cols="12" md="2" order="last" order-md="first">
          <wra-button
            dark
            class="mr-4 mb-2"
            @click="previousStage"
            v-if="stage.StageID != 1"
          >
            <wra-icon alt="" left large>navigate_before</wra-icon>
            <span v-html="text.previousButton"></span>
          </wra-button>
        </v-col>
      </v-row>
    </wra-card-actions>
  </wra-card>
</template>

<script>
export default {
  props: ["stage", "text"],
  data: () => ({
    componentName: "CheckerQuestion",
    showValidation: false
  }),
  watch: {
    stage: {
      deep: true,
      handler() {
        if (this.stage.selectedAnswer) {
          this.showValidation = false;
        }
      }
    }
  },
  methods: {
    nextStage: function() {
      if (!this.stage.selectedAnswer) {
        this.showValidation = true;
        this.$refs.topofcard.focus();
      } else {
        this.$emit("next", this.stage.selectedAnswer);
      }
    },
    previousStage: function() {
      this.$emit("previous");
    }
  }
};
</script>
