<template>
  <wra-app v-if="isValidBrowser">
    <a href="#maincontent" class="skip">Skip to main content</a>
    <!--Cookie Control-->
    <wra-banner
      v-model="cookieBanner"
      color="#f1f1f1"
      v-if="cookieExist() == false"
      class="hidden-print"
    >
      <v-container>
        <v-row no-gutters>
          <v-col lg="2" cols="0"></v-col>
          <v-col lg="8" cols="12">
            {{ appText.cookieControl.cookieBannerText }}
            <div class="pt-5">
              <wra-button small outlined class="mr-4" @click="acceptAllCookies">
                {{ appText.cookieControl.acceptCookiesText }}
              </wra-button>
              <wra-button small outlined @click="goToCookies">
                {{ appText.cookieControl.setCookiePrefText }}
              </wra-button>
            </div>
          </v-col>
          <v-col lg="2" cols="0"></v-col>
        </v-row>
      </v-container>
    </wra-banner>

    <!--Accepted acknowledgement-->
    <wra-alert
      v-model="cookieAlert"
      v-if="!cookieBanner && this.$route.name != 'cookies'"
      close-text="Close Alert"
      close-icon="mdi-domain"
      color="#f1f1f1"
      class="hidden-print"
    >
      <v-container>
        <v-row no-gutters>
          <v-col lg="2" cols="0"></v-col>
          <v-col lg="7" cols="12">
            {{ appText.cookieControl.cookieAlertTextP1 }}
            <router-link to="/cookies">
              {{ appText.cookieControl.cookieAlertLinkText }}
            </router-link>
            {{ appText.cookieControl.cookieAlertTextP2 }}
          </v-col>

          <v-col lg="1">
            <wra-icon
              @click="hideAlert"
              tabindex="0"
              color="#1f1f1f"
              style="float: right;"
              >mdi-close-thick</wra-icon
            >
          </v-col>

          <v-col lg="2" cols="0"></v-col>
        </v-row>
      </v-container>
    </wra-alert>

    <!--App Bar-->
    <wra-app-bar
      scroll-off-screen
      flat
      dark
      class="hidden-print"
      height="74"
      color="#323232"
    >
      <v-container
        :class="{
          'px-0': $vuetify.breakpoint.smAndDown
        }"
      >
        <v-row no-gutters>
          <v-col lg="2" cols="0" style="text-align:left;"></v-col>
          <v-col lg="4" cols="6">
            <img
              src="../public/img/wg-logo-white.svg"
              alt="Welsh Government Logo"
              height="34"
            />
          </v-col>

          <v-col lg="4" cols="6" style="text-align:right;">
            <wra-button
              dark
              large
              height="34"
              color="#1f1f1f"
              @click="languageFlipper"
              class="language-flipper"
            >
              {{ appText.langButton }}
            </wra-button>
          </v-col>
          <v-col lg="2" cols="0"></v-col>
        </v-row>
      </v-container>
    </wra-app-bar>

    <!--Feedback Control-->
    <router-view name="feedback" :text="appText.feedbackControl" />

    <nav>
      <router-view name="breadcrumbs" :crumbs="appText.breadcrumbs" />
    </nav>

    <v-main id="maincontent" tabindex="-1">
      <!--WRA Logo & Divider-->
      <v-container
        class="hidden-print"
        :class="{
          'px-5': $vuetify.breakpoint.smAndDown
        }"
      >
        <v-row no-gutters>
          <v-col lg="2" cols="0"></v-col>
          <v-col lg="8" cols="12" class="mt-8">
            <transition name="translate" mode="out-in">
              <wra-service-logo
                :label="appText.wraLogo.logoLinkText"
                :alt="appText.wraLogo.logoAltText"
                :service-logo="appText.wraLogo.logoFilePath"
                :class="{
                  'wra-logo-xs-only': $vuetify.breakpoint.xsOnly,
                  'wra-logo-sm-and-up': $vuetify.breakpoint.smAndUp
                }"
                :key="appText.wraLogo.logoLinkText"
                :href="appText.wraLogo.logoHref"
              ></wra-service-logo>
            </transition>
          </v-col>
          <v-col lg="2" cols="0"></v-col>
        </v-row>
      </v-container>

      <router-view :lang="this.$root.$data.lang" />
    </v-main>

    <!--Footer-->
    <wra-footer class="hidden-print">
      <v-container
        class="py-0"
        :class="{
          'px-0': $vuetify.breakpoint.smAndDown
        }"
      >
        <v-row no-gutters>
          <v-col lg="2" cols="0"></v-col>
          <v-col lg="8" cols="12">
            <span class="mt-2 mr-4 ml-0 mb-0 footer-link-wrap">
              <a
                :href="appText.footer.accessibilityLink"
                target="_blank"
                rel="noreferrer"
                :aria-label="
                  appText.footer.accessibilityText +
                    ' (' +
                    appText.footer.newTab +
                    ')'
                "
              >
                {{ appText.footer.accessibilityText }}
                <wra-icon class="ml-1 mb-1" color="#1f1f1f" small>
                  mdi-open-in-new
                </wra-icon>
              </a>
            </span>
            <span class="mt-2 mr-4 ml-0 mb-0 footer-link-wrap">
              <router-link to="/cookies">{{
                appText.footer.cookiesText
              }}</router-link>
            </span>
            <span class="mt-2 mr-4 ml-0 mb-0 footer-link-wrap">
              <a
                :href="appText.footer.copyrightLink"
                target="_blank"
                rel="noreferrer"
                :aria-label="
                  appText.footer.copyrightText +
                    ' (' +
                    appText.footer.newTab +
                    ')'
                "
                >{{ appText.footer.copyrightText }}
                <wra-icon class="ml-1 mb-1" color="#1f1f1f" small
                  >mdi-open-in-new</wra-icon
                >
              </a>
            </span>
            <span class="mt-2 mr-4 ml-0 mb-0 footer-link-wrap">
              <a
                :href="appText.footer.privacyLink"
                target="_blank"
                rel="noreferrer"
                :aria-label="
                  appText.footer.privacyText +
                    ' (' +
                    appText.footer.newTab +
                    ')'
                "
                >{{ appText.footer.privacyText }}
                <wra-icon class="ml-1 mb-1" color="#1f1f1f" small
                  >mdi-open-in-new</wra-icon
                >
              </a>
            </span>
            <span class="mt-2 mr-4 ml-0 mb-0 footer-link-wrap">
              <a
                :href="appText.footer.termsLink"
                target="_blank"
                rel="noreferrer"
                :aria-label="
                  appText.footer.termsText + ' (' + appText.footer.newTab + ')'
                "
                >{{ appText.footer.termsText }}
                <wra-icon class="ml-1 mb-1" color="#1f1f1f" small
                  >mdi-open-in-new</wra-icon
                >
              </a>
            </span>
            <div style="margin: 90px 0px 0px 0px;">
              <wra-wg-service-logo
                :label="appText.wgLogo.logoLinkText"
                :alt="appText.wgLogo.logoAltText"
                :service-logo="appText.wgLogo.logoFilePath"
                :href="appText.wgLogo.logoHref"
              ></wra-wg-service-logo>
            </div>
          </v-col>
          <v-col lg="2" cols="0"></v-col>
        </v-row>
      </v-container>
    </wra-footer>
  </wra-app>
  <wra-app v-else>
    <wra-validation-tooltip
      type="wra-error"
      role="status"
      class="is-valid-browser"
    >
      <p class="pb-4">
        Nid yw Internet Explorer yn cael ei gefnogi mwyach, cyfeiriwch at ein
        <a
          href="https://llyw.cymru/defnyddio-porwyr-ar-gyfer-gwasanaethau-ar-lein-awdurdod-cyllid-cymru"
          target="_blank"
          rel="noreferrer"
          aria-label=""
          >canllawiau</a
        >
        i weld rhestr o'n porwyr a gefnogir.
      </p>

      <p>
        Internet Explorer is no longer supported, please refer to our
        <a
          href="https://gov.wales/using-browsers-welsh-revenue-authority-online-services"
          target="_blank"
          rel="noreferrer"
          aria-label=""
          >guidance</a
        >
        to see a list of our supported browsers.
      </p>
    </wra-validation-tooltip>
  </wra-app>
</template>

<script>
const axios = require("axios");

export default {
  name: "App",

  data: () => ({
    isValidBrowser: false,
    cookieBanner: true,
    cookieAlert: false,
    appText: {
      cookieControl: {},
      feedbackControl: {},
      breadcrumbs: [],
      wraLogo: {},
      wgLogo: {},
      helpGuidance: {},
      footer: {}
    }
  }),
  methods: {
    goToCookies: function() {
      this.$router.push("cookies");
    },

    getText: function() {
      if (
        document.cookie.split("; ").find((row) => row.startsWith("langControl"))
      ) {
        var langCookie = document.cookie
          .split("; ")
          .find((row) => row.startsWith("langControl"));
        this.$root.$data.lang = langCookie.split("=").pop();
      }

      axios
        .get("./text/" + this.$root.$data.lang + "/app.json")
        .then((response) => {
          this.appText = response.data.appText;
        });
    },

    languageFlipper: function() {
      if (this.$root.$data.lang == "english") {
        this.$root.$data.lang = "cymraeg";
      } else {
        this.$root.$data.lang = "english";
      }
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("Site Settings", "Set Language", this.$root.$data.lang);
      }
      var d = new Date();
      d.setFullYear(d.getFullYear() + 1);

      document.cookie =
        "langControl=" +
        this.$root.$data.lang +
        "; expires=" +
        d.toUTCString() +
        ";";

      this.getText();
    },

    cookieExist: function() {
      if (this.$route.name == "cookies") {
        return true;
      } else if (
        document.cookie
          .split("; ")
          .find((row) => row.startsWith("TmsCookieControl"))
      ) {
        return true;
      } else {
        return false;
      }
    },

    hideAlert: function() {
      this.cookieAlert = false;
    },

    readControlCookie: function() {
      if (
        document.cookie
          .split("; ")
          .find((row) => row.startsWith("TmsCookieControl"))
      ) {
        var controlCookie = document.cookie
          .split("; ")
          .find((row) => row.startsWith("TmsCookieControl"));
        //this.$root.$data.cookieAccept = JSON.parse(controlCookie.split("=").pop());
        var cookieValue = controlCookie.split("=").pop();
        var cookieArray = cookieValue.split(":");
        this.$root.$data.cookieAccept.websiteUse = cookieArray[1];
        this.$root.$data.cookieAccept.performance = cookieArray[3].substring(
          0,
          cookieArray[3].length - 1
        );

        if (this.$root.$data.cookieAccept.websiteUse == true) {
          this.$ga.enable();
        } else {
          this.$ga.disable();
        }
      } else {
        this.$ga.disable();
      }
    }
  },

  mounted() {
    var ua = window.navigator.userAgent;
    //Check for Internet Explorer 11 or older
    function isIE() {
      var msie = ua.indexOf("MSIE "); //IE 10 or older
      var trident = ua.indexOf("Trident/"); //IE 11
      return msie > 0 || trident > 0;
    }
    if (isIE()) {
      this.isValidBrowser = false;
    } else {
      this.isValidBrowser = true;
      this.getText();
      this.readControlCookie();
    }
  }
};
</script>

<style>
main {
  border: none;
  outline: none;
}

.skip {
  position: absolute;
  overflow: hidden;
  top: -1px;
  width: 1px;
  height: 1px;
}

.skip:focus {
  position: relative;
  top: auto;
  width: auto;
  height: auto;
  overflow: auto;
}

legend,
caption {
  position: absolute !important;
  overflow: hidden !important;
  white-space: nowrap !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
}

.v-application--is-ltr .v-banner__actions > * {
  margin: auto !important;
}

.language-flipper:hover {
  background-color: #666666 !important;
}

.give-feedback:focus {
  color: #ffffff !important;
  background-color: #1f1f1f !important;
  box-shadow: 0 -4px #1f1f1f, 0 2px #ffffff !important;
  outline: 2px solid transparent !important;
  text-decoration: none !important;
  -webkit-box-shadow: 0 -4px #1f1f1f, 0 2px #ffffff !important;
}

.minimum-height {
  min-height: 400px;
}

.question-summary {
  font-weight: bold;
}

.answer-summary {
  line-height: 44px;
}

.further-information-text {
  font-size: 14px;
}

.hint-text {
  color: #666666;
}

.wra-logo-sm-and-up {
  width: 50%;
}

.wra-logo-xs-only {
  width: 100%;
}

.ltt-price-field {
  width: 190px;
  padding-top: 0px !important;
}

.ltt-date-field {
  width: 148px;
}

.calc-result {
  background-color: #e5e5e5;
  border-left: 10px solid #0360a6;
}

.calc-result-header {
  font-size: 18px !important;
  font-weight: normal !important;
}

.calc-result-data {
  font-size: 36px !important;
  font-weight: bold !important;
}

.calc-result-data-sm {
  font-size: 28px !important;
  font-weight: bold !important;
}

.calc-result:hover {
  background-color: #e5e5e5 !important;
}

.calc-breakdown-total {
  font-size: 22px !important;
  font-weight: bold !important;
}

.cookie-validation-link-text {
  text-decoration: underline;
}

.footer-link-wrap {
  display: inline-block;
}

.translate-enter-active {
  transition: all 0.2s ease;
}

.translate-leave-active {
  transition: all 0s ease;
}

.translate-enter,
.translate-leave-to {
  opacity: 0;
  transform: translateX(-10px);
}

.translate-enter-to,
.translate-leave {
  opacity: 1;
  transform: translateX(0px);
}

@media print {
  .hidden-print {
    display: none !important;
  }
}

.is-valid-browser {
  margin: 50px auto;
  width: 960px;
}
</style>
