import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import vuetify from "./plugins/vuetify";
import VueAnalytics from "vue-analytics";
import upperFirst from "lodash/upperFirst";
import camelCase from "lodash/camelCase";
import VueMask from "v-mask";

const requireComponent = require.context(
  // The relative path of the components folder
  "./components/base",
  // Whether or not to look in subfolders
  false,
  // The regular expression used to match base component filenames
  /Wra[A-Z]\w+\.(vue|js)$/
);

requireComponent.keys().forEach((fileName) => {
  // Get component config
  const componentConfig = requireComponent(fileName);

  // Get PascalCase name of component
  const componentName = upperFirst(
    camelCase(
      // Gets the file name regardless of folder depth
      fileName
        .split("/")
        .pop()
        .replace(/\.\w+$/, "")
    )
  );

  // Register component globally
  Vue.component(
    componentName,
    // Look for the component options on `.default`, which will
    // exist if the component was exported with `export default`,
    // otherwise fall back to module's root.
    componentConfig.default || componentConfig
  );
});

Vue.config.productionTip = false;

Vue.mixin({
  computed: {
    computedCookies: function() {
      return this.$root.$data.cookieAccept;
    }
  },

  methods: {
    acceptAllCookies: function() {
      this.$root.$data.cookieAccept = {
        websiteUse: "true",
        performance: "true"
      };

      this.cookieBanner = false;
      this.cookieAlert = true;
      this.controlCookies();
    },

    controlCookies: function() {
      var d = new Date();
      d.setFullYear(d.getFullYear() + 1);

      //var cookieAcceptString = JSON.stringify(this.$root.$data.cookieAccept);
      var cookieAcceptString =
        "{ga:" +
        this.$root.$data.cookieAccept.websiteUse +
        ":ai:" +
        this.$root.$data.cookieAccept.performance +
        "}";

      document.cookie =
        "TmsCookieControl=" +
        cookieAcceptString +
        "; domain=" +
        location.hostname.substring(location.hostname.indexOf(".")) +
        "; expires=" +
        d.toUTCString() +
        ";";

      if (this.$root.$data.cookieAccept.websiteUse == true) {
        this.$ga.enable();
      } else {
        this.$ga.disable();
      }
    }
  },

  watch: {
    computedCookies: {
      handler() {
        this.$root.$data.showValidation = false;
      },
      deep: true
    }
  }
});

var cookieData = {
  cookieAccept: {
    websiteUse: "false",
    performance: "false"
  },
  showValidation: false,
  lang: "english"
};

// Configuration VueAnalytics
Vue.use(VueAnalytics, {
  id: "UA-110224862-10",
  router
});

var VueScrollTo = require("vue-scrollto");
Vue.use(VueScrollTo);

Vue.use(VueMask);

new Vue({
  data: cookieData,
  router,
  vuetify,
  render: (h) => h(App)
}).$mount("#app");
