<template>
  <wra-card
    :class="{
      'px-5 py-0': $vuetify.breakpoint.smAndDown,
      'pa-0': $vuetify.breakpoint.mdAndUp
    }"
    color="#FFFFFF"
    outlined
  >
    <wra-card-title class="pt-4 px-0 pb-0">
      <h2 v-html="stage.StageTitle"></h2>
    </wra-card-title>

    <wra-card-text class="py-4 px-0">
      <p v-html="stage.StageText"></p>
    </wra-card-text>

    <!-- 'Results based on the following answers' Q&A summary expansion panel (FooterText) -->
    <wra-card-text class="py-4 px-0">
      <wra-expansion-panels flat>
        <wra-expansion-panel>
          <wra-expansion-panel-header>
            {{ text.basedOnAnswers }}
          </wra-expansion-panel-header>

          <wra-expansion-panel-content>
            <div
              v-for="p in previousStages"
              v-bind:key="p.StageID"
              class="pb-4"
            >
              <p
                v-html="p.StageEnglish.StageTitle"
                class="question-summary"
                v-if="lang == 'english'"
              ></p>
              <p
                v-html="p.StageCymraeg.StageTitle"
                class="question-summary"
                v-else
              ></p>
              <p v-html="p.AnswerText" class="answer-summary"></p>
            </div>
          </wra-expansion-panel-content>
        </wra-expansion-panel>
      </wra-expansion-panels>
    </wra-card-text>

    <!--Result page FooterText (no expansion panel)-->
    <wra-card-text class="py-4 px-0">
      <p v-html="stage.FooterText"></p>
    </wra-card-text>

    <wra-card-actions class="py-2 pb-0 pt-5">
      <v-row>
        <v-col cols="12" md="2" order="first" order-md="last" class="pl-1">
          <wra-button dark class="mb-2" @click="firstStage">
            {{ text.startAgainButton }}
          </wra-button>
        </v-col>
        <v-col cols="12" md="2" order="last" order-md="first" class="pl-1">
          <wra-button
            dark
            class="mr-4 mb-2"
            @click="previousStage"
            v-if="stage.StageID != 1"
          >
            <wra-icon alt="" left large>navigate_before</wra-icon>
            <span v-html="text.previousButton"></span>
          </wra-button>
        </v-col>
      </v-row>
    </wra-card-actions>
  </wra-card>
</template>

<script>
export default {
  props: ["lang", "stage", "text", "previousStages"],
  data: () => ({
    componentName: "CheckerResult"
  }),
  methods: {
    previousStage: function() {
      this.$emit("previous");
    },
    firstStage: function() {
      this.$emit("restart");
    }
  }
};
</script>
