<template>
  <form @submit.prevent="nextStage">
    <wra-card
      :class="{
        'px-1 py-1': $vuetify.breakpoint.smAndDown,
        'px-6 py-5': $vuetify.breakpoint.mdAndUp
      }"
      color="#F1F1F1"
      outlined
    >
      <transition name="translate" mode="out-in">
        <wra-card-text
          v-if="showValidation"
          class="pa-4"
          :key="text.transactionDate.title"
        >
          <wra-validation-tooltip
            type="wra-error"
            role="status"
            aria-live="polite"
          >
            {{ validationMessage }}
          </wra-validation-tooltip>
        </wra-card-text>
      </transition>

      <transition name="translate" mode="out-in">
        <wra-card-text class="pa-4" :key="text.transactionDate.title">
          <wra-card-title class="pt-0 px-0 pb-4">
            <h2 v-html="text.transactionDate.title"></h2>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-5">
            <p v-html="text.transactionDate.description"></p>
          </wra-card-text>
          <wra-checkbox
            v-model="isToday"
            :label="text.transactionDate.checkbox"
            color="#29235C"
            id="dateCheckbox"
            @click="checkboxClicked()"
          ></wra-checkbox>
          <wra-text-field
            v-model="localDateString"
            v-mask="'##/##/####'"
            :placeholder="text.transactionDate.placeholder"
            class="ltt-date-field"
            id="dateField"
            inputmode="numeric"
          ></wra-text-field>
        </wra-card-text>
      </transition>

      <transition name="translate" mode="out-in">
        <wra-card-actions class="py-2 px-4" :key="text.transactionDate.title">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button dark class="mb-2" id="nextButton" type="submit">
                {{ text.next }}
                <wra-icon alt="" right large>navigate_next</wra-icon>
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButton"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </transition>
    </wra-card>
  </form>
</template>

<script>
export default {
  props: ["text", "inputs"],

  data: () => ({
    showValidation: false,
    isValid: true,
    validationMessage: null,
    localInputs: {},
    localDateString: null,
    isToday: false
  }),
  watch: {
    text: {
      deep: true,
      handler() {
        this.validatePage();
      }
    },
    localDateString: {
      handler() {
        var today = new Date().setHours(0, 0, 0, 0);
        var localDate = new Date(
          this.localDateString.split("/")[1] +
            "/" +
            this.localDateString.split("/")[0] +
            "/" +
            this.localDateString.split("/")[2]
        ).setHours(0, 0, 0, 0);
        if (today == localDate) {
          this.isToday = true;
        } else {
          this.isToday = false;
        }
        this.validatePage();
      }
    },
    isToday: {
      handler() {
        if (this.isToday == true) {
          this.localDateString = new Intl.DateTimeFormat("en-GB").format(
            new Date()
          );
        }
        this.validatePage();
      }
    }
  },
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "What is the effective date?", "Back");
      }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      this.validatePage();

      if (this.isValid == true) {
        this.showValidation = false;
        if (this.$root.$data.cookieAccept.websiteUse == "true") {
          this.$ga.event(
            "LTT Calculator",
            "What is the effective date?",
            "Next"
          );
        }
        this.$emit("next", this.localInputs);
      } else {
        this.showValidation = true;
        this.$emit("scrollUp");
      }
    },
    validatePage: function() {
      if (this.localDateString == null) {
        this.validationMessage = this.text.transactionDate.validation.invalidDate;
        this.isValid = false;
      } else {
        var lttStart = new Date(2018, 3, 1); //1st April 2018 (Jan = 0)

        // We need to do this bit because JS thinks dates are written month/day/year, so we switch the first two around.
        var localDate = new Date(
          this.localDateString.split("/")[1] +
            "/" +
            this.localDateString.split("/")[0] +
            "/" +
            this.localDateString.split("/")[2]
        );

        var enteredMonth = parseInt(this.localDateString.split("/")[1]) - 1;

        if (localDate == "Invalid Date") {
          this.validationMessage = this.text.transactionDate.validation.invalidDate;
          this.isValid = false;
        } else if (enteredMonth != localDate.getMonth()) {
          this.validationMessage = this.text.transactionDate.validation.invalidDate;
          this.isValid = false;
        } else if (localDate < lttStart) {
          this.validationMessage = this.text.transactionDate.validation.preLtt;
          this.isValid = false;
        } else {
          this.isValid = true;
          this.localInputs.transactionDate = new Intl.DateTimeFormat(
            "en-GB"
          ).format(localDate);
        }
      }
    },
    checkboxClicked: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event(
          "LTT Calculator",
          "Use todays date",
          this.isToday.toString()
        );
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));

    // Need code here to create a formatted string of the date for the text field, to add to 'localDateString'
    if (this.localInputs.transactionDate) {
      this.localDateString = this.localInputs.transactionDate;
    }
  },
  mounted() {
    this.$emit("scrollUp");
  }
};
</script>

<style scoped>
.v-input {
  font-size: 18px;
}
</style>
