<template>
  <v-container
    class="pt-0 pb-3"
    :class="{
      'px-0': $vuetify.breakpoint.smAndDown
    }"
  >
    <!--Heading-->
    <v-row
      no-gutters
      class="pb-6"
      :class="{
        'px-5': $vuetify.breakpoint.smAndDown
      }"
    >
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <v-divider
          role="presentation"
          style="height: 6px;
          max-height: 6px;
          background: #e5e5e5;
          border: none;"
        ></v-divider>

        <a ref="topofpage" href="#" tabindex="-1"></a>

        <wra-subheader class="mt-8">
          {{ cookiesText.beforeHeadingText }}
        </wra-subheader>
        <h1>
          {{ cookiesText.headingText }}
        </h1>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <div
          class="pb-6"
          :class="{
            'px-5': $vuetify.breakpoint.smAndDown
          }"
        >
          <p v-html="cookiesText.cookiesDescription"></p>
        </div>

        <wra-card
          class="mb-15"
          :class="{
            'px-1 py-1': $vuetify.breakpoint.smAndDown,
            'px-6 py-5': $vuetify.breakpoint.mdAndUp
          }"
          color="#f1f1f1"
          outlined
        >
          <wra-card-title>
            <h2 class="pb-8">
              {{ cookiesText.cookieSettingsHeading }}
            </h2>
          </wra-card-title>

          <wra-card-text>
            <h3 class="pb-4">
              {{ cookiesText.websiteUseCookies.headingText }}
            </h3>

            <div>
              <p v-html="cookiesText.websiteUseCookies.descriptionText"></p>
            </div>

            <wra-radio-group
              v-model="$root.$data.cookieAccept.websiteUse"
              row
              class="pt-4"
            >
              <template v-slot:label>
                {{ cookiesText.websiteUseCookies.headingText }}
              </template>
              <wra-radio
                :label="cookiesText.websiteUseCookies.onLabel"
                value="true"
              ></wra-radio>
              <wra-radio
                :label="cookiesText.websiteUseCookies.offLabel"
                value="false"
              ></wra-radio>
            </wra-radio-group>
          </wra-card-text>

          <wra-card-text class="pt-0 px-4 pb-8">
            <wra-expansion-panels flat>
              <wra-expansion-panel>
                <wra-expansion-panel-header>
                  {{ cookiesText.websiteUseCookies.expansionPanelText }}
                </wra-expansion-panel-header>
                <wra-expansion-panel-content>
                  <div>
                    <wra-simple-table>
                      <template v-slot:default>
                        <caption>
                          {{
                            cookiesText.websiteUseCookies.expansionPanelText
                          }}
                        </caption>
                        <thead>
                          <tr>
                            <th class="text-left">
                              {{ cookiesText.websiteUseCookies.tableName }}
                            </th>
                            <th class="text-left">
                              {{ cookiesText.websiteUseCookies.tablePurpose }}
                            </th>
                            <th class="text-left">
                              {{ cookiesText.websiteUseCookies.tableExpires }}
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr
                            v-for="item in cookiesText.websiteUseCookies
                              .tableContent"
                            :key="item.name"
                          >
                            <td>{{ item.name }}</td>
                            <td>{{ item.purpose }}</td>
                            <td>{{ item.expires }}</td>
                          </tr>
                        </tbody>
                      </template>
                    </wra-simple-table>
                  </div>
                </wra-expansion-panel-content>
              </wra-expansion-panel>
            </wra-expansion-panels>
          </wra-card-text>

          <wra-card-text>
            <h3 class="pb-4">
              {{ cookiesText.performanceCookies.headingText }}
            </h3>

            <div>
              <p>{{ cookiesText.performanceCookies.descriptionText }}</p>
            </div>

            <wra-radio-group
              v-model="$root.$data.cookieAccept.performance"
              row
              class="pt-4"
            >
              <template v-slot:label>
                {{ cookiesText.performanceCookies.headingText }}
              </template>
              <wra-radio
                :label="cookiesText.performanceCookies.onLabel"
                value="true"
              ></wra-radio>
              <wra-radio
                :label="cookiesText.performanceCookies.offLabel"
                value="false"
              ></wra-radio>
            </wra-radio-group>
          </wra-card-text>

          <wra-card-text class="pt-0 px-4 pb-8">
            <wra-expansion-panels flat>
              <wra-expansion-panel>
                <wra-expansion-panel-header>
                  {{ cookiesText.performanceCookies.expansionPanelText }}
                </wra-expansion-panel-header>
                <wra-expansion-panel-content>
                  <div>
                    <wra-simple-table>
                      <template v-slot:default>
                        <caption>
                          {{
                            cookiesText.performanceCookies.expansionPanelText
                          }}
                        </caption>
                        <thead>
                          <tr>
                            <th class="text-left">
                              {{ cookiesText.performanceCookies.tableName }}
                            </th>
                            <th class="text-left">
                              {{ cookiesText.performanceCookies.tablePurpose }}
                            </th>
                            <th class="text-left">
                              {{ cookiesText.performanceCookies.tableExpires }}
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr
                            v-for="item in cookiesText.performanceCookies
                              .tableContent"
                            :key="item.name"
                          >
                            <td>{{ item.name }}</td>
                            <td>{{ item.purpose }}</td>
                            <td>{{ item.expires }}</td>
                          </tr>
                        </tbody>
                      </template>
                    </wra-simple-table>
                  </div>
                </wra-expansion-panel-content>
              </wra-expansion-panel>
            </wra-expansion-panels>
          </wra-card-text>

          <wra-card-text>
            <h3 class="pb-4">
              {{ cookiesText.necessaryCookies.headingText }}
            </h3>

            <div>
              <p>{{ cookiesText.necessaryCookies.descriptionText }}</p>
            </div>
          </wra-card-text>

          <wra-card-text class="pt-5 px-4 pb-8">
            <wra-expansion-panels flat>
              <wra-expansion-panel>
                <wra-expansion-panel-header>
                  {{ cookiesText.necessaryCookies.expansionPanelText }}
                </wra-expansion-panel-header>
                <wra-expansion-panel-content>
                  <div>
                    <wra-simple-table>
                      <template v-slot:default>
                        <caption>
                          {{
                            cookiesText.necessaryCookies.expansionPanelText
                          }}
                        </caption>
                        <thead>
                          <tr>
                            <th class="text-left">
                              {{ cookiesText.necessaryCookies.tableName }}
                            </th>
                            <th class="text-left">
                              {{ cookiesText.necessaryCookies.tablePurpose }}
                            </th>
                            <th class="text-left">
                              {{ cookiesText.necessaryCookies.tableExpires }}
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr
                            v-for="item in cookiesText.necessaryCookies
                              .tableContent"
                            :key="item.name"
                          >
                            <td>{{ item.name }}</td>
                            <td>{{ item.purpose }}</td>
                            <td>{{ item.expires }}</td>
                          </tr>
                        </tbody>
                      </template>
                    </wra-simple-table>
                  </div>
                </wra-expansion-panel-content>
              </wra-expansion-panel>
            </wra-expansion-panels>
          </wra-card-text>

          <!--Validation-->
          <wra-card-text v-if="$root.$data.showValidation" class="px-4 pb-9">
            <wra-validation-tooltip type="wra-success" role="status">
              {{ cookiesText.validationText }}
              <a
                @click="$router.go(-1)"
                class="cookie-validation-link-text"
                tabindex="0"
              >
                {{ cookiesText.validationLinkText }}
              </a>
            </wra-validation-tooltip>
          </wra-card-text>

          <wra-card-actions class="pa-4 hidden-print">
            <v-row>
              <v-col cols="12" md="4" xl="3">
                <wra-button
                  dark
                  color="#0360a6"
                  @click="controlCookieValidation"
                >
                  {{ cookiesText.saveChangesButton }}
                </wra-button>
              </v-col>
              <v-col cols="12" md="4" xl="3">
                <wra-button dark @click="$router.go(-1)">
                  {{ cookiesText.backButton }}
                </wra-button>
              </v-col>
            </v-row>
          </wra-card-actions>
        </wra-card>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>
  </v-container>
</template>

<script>
const axios = require("axios");

export default {
  props: ["lang"],

  data: () => ({
    cookiesText: {
      websiteUseCookies: {},
      performanceCookies: {},
      necessaryCookies: {}
    }
  }),

  watch: {
    lang: {
      handler() {
        this.getText();
      }
    }
  },

  methods: {
    getText: function() {
      axios.get("./text/" + this.lang + "/cookies.json").then((response) => {
        this.cookiesText = response.data.cookiesText;
        document.title = this.cookiesText.documentTitleText;
        this.$refs.topofpage.focus();
      });
    },
    controlCookieValidation: function() {
      this.controlCookies();
      this.$root.$data.showValidation = true;
    }
  },

  mounted() {
    this.getText();
    // Try to find TmsCookieControl
    // If it doesn't exist, set cookies to 'Off'
    if (
      !document.cookie
        .split("; ")
        .find((row) => row.startsWith("TmsCookieControl"))
    ) {
      this.$root.$data.cookieAccept.performance = "false";
      this.$root.$data.cookieAccept.websiteUse = "false";
    }
  },

  destroyed() {
    this.$root.$data.showValidation = false;
  }
};
</script>

<style></style>
